.sidebar {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 87px;
  height: 100%;
  background-color: #002341;
  overflow-y: auto;
  overflow-x: hidden;
  &::-webkit-scrollbar {
    width: 0;
  }
  &::-webkit-scrollbar-track {
    background: #000000;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #ffffff;
    border-radius: 3px;
  }
  &-inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &-burger {
    width: 34px;
    height: 26px;
    position: relative;
    z-index: 20;
    cursor: pointer;
    margin-top: 35px;
    &:after {
      content: '';
      position: absolute;
      top: -35px;
      left: 50%;
      transform: translate(-50%, 0);
      width: 87px;
      height: 91px;
      background-color: #003766;
    }
    &:before {
      content: '';
      position: absolute;
      top: -35px;
      left: 50%;
      transform: translate(-50%, 0);
      width: 1px;
      height: 115px;
      background: #ff5700;
    }
    span {
      position: absolute;
      z-index: 1;
      left: 0;
      width: 100%;
      height: 3px;
      background: #ffffff;
      border-radius: 3px;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span:nth-child(1) {
        top: 11px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 12px;
        transform: rotate(45deg);
      }
    }
  }
  &-search {
    width: 27px;
    cursor: pointer;
    margin-top: 80px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: calc(100% + 27px);
      left: 50%;
      transform: translate(-50%, 0);
      width: 1px;
      height: 172px;
      background: #ff5700;
    }
    img {
      width: 100%;
    }
  }
  &-socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 228px;
  }
  &-social {
    max-width: 25px;
    &:not(:first-child) {
      margin-top: 18px;
    }
    img {
      max-width: 100%;
    }
  }
  &-btn {
    width: 51px;
    height: 254px;
    background: transparent;
    border: 2px solid #ff5700;
    margin-top: 245px;
    position: relative;
    &:hover {
      background: #ff5700;
    }
    span {
      font-size: 12px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.06em;
      color: #ffffff;
      writing-mode: vertical-rl;
      transform: rotate(180deg);
    }
    &:after {
      content: '';
      position: absolute;
      top: -220px;
      left: 50%;
      transform: translate(-50%, 0);
      width: 1px;
      height: 220px;
      background: #ff5700;
    }
    &:before {
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translate(-50%, 0);
      width: 1px;
      height: 220px;
      background: #ff5700;
    }
  }
}
