    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: -30px;
      width: 5px;
      height: 100%;
      background: #ff5700;
    }
  }
  &__opacity {
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s;
    margin-top: 50px;
  }
