.footer {
  background-color: #002341;
  padding: 50px 0 20px;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    margin-bottom: 30px;
    &:not(:last-child) {
      margin-right: 30px;
    }
    &-show {
      display: flex;
      align-items: center;
    }
    &-title {
      font-size: 20px;
      line-height: 120%;
      font-weight: 700;
      color: #ffffff;
    }
    &-arrow {
      display: none;
      width: 10px;
      margin-left: 10px;
      img {
        width: 100%;
      }
    }
    &-hidden {
      margin-top: 15px;
    }
    &-link {
      display: block;
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 10px;
      }
      &:hover {
        color: #ff5700;
      }
    }
  }
  &__desc {
    margin-bottom: 30px;
  }
  &__rights {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    color: #7cc3ff;
    a {
      display: block;
      font-weight: 400;
    }
  }
  &__brevis {
    display: flex;
    align-items: center;
    margin-top: 15px;
    &-icon {
      width: 25px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 12px;
      line-height: 120%;
      color: #7cc3ff;
      margin-left: 10px;
      a {
        display: block;
        font-weight: 700;
        text-decoration: underline;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    padding: 30px 0;
    &__inner {
      display: block;
    }
    &__item {
      margin: 0 0 30px 0;
      &-show {
        cursor: pointer;
      }
      &-title {
        font-size: 16px;
      }
      &-arrow {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      &-hidden {
        display: none;
      }
      &-link {
        font-size: 12px;
      }
    }
    &__desc {
      margin: 0;
    }
    &__rights {
      font-size: 12px;
    }
    &__brevis {
      &-icon {
        width: 20px;
      }
      &-text {
        font-size: 12px;
      }
    }
  }
}