.header {
  position: absolute;
  z-index: 3;
  top: 0;
  width: 100%;
  .mail,
  .location,
  .tel {
    &:hover {
      .header-top__item-text {
        color: #ff5700;
      }
    }
  }
  .tel {
    .header-top__item-text {
      font-weight: 700;
    }
  }
  &-burger {
    width: 34px;
    height: 26px;
    position: relative;
    z-index: 20;
    cursor: pointer;
    display: none;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background: #ff5700;
      border-radius: 3px;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      span:nth-child(1) {
        top: 11px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 12px;
        transform: rotate(45deg);
      }
    }
  }
}
.header-top {
  padding: 15px 0 5px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    margin-bottom: 10px;
  }
  &__center {
    margin: 0 50px;
    flex-grow: 1;
  }
  &__right {
    margin-bottom: 10px;
  }
  &__logo {
    display: block;
    width: 243px;
    img {
      width: 100%;
    }
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__group {
    margin-bottom: 10px;
    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  &__item {
    display: flex;
    &:not(:first-child) {
      margin-top: 10px;
    }
    &-icon {
      width: 20px;
      img {
        max-width: 100%;
      }
    }
    &-text {
      font-size: 18px;
      line-height: 120%;
      color: #ffffff;
      margin-left: 10px;
      transition: all 0.5s;
    }
  }
  &__btn {
    width: 199px;
    height: 36px;
    span {
      font-size: 12px;
    }
  }
}
.header-bottom {
  background-color: rgba(0, 50, 93, 0.4);;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 15px 0;
  &__list {
    display: flex;
    align-items: center;
    li {
      &:not(:first-child) {
        margin-left: 15px;
      }
    }
  }
  &__link {
    font-size: 15px;
    line-height: 120%;
    font-weight: 600;
    color: #ffffff;
    a {
      display: flex;
      align-items: center;
    }
    a span {
      display: block;
      width: 5px;
      height: 5px;
      background: #ffffff;
      border-radius: 50%;
      opacity: 0.2;
      margin-left: 15px;
      transition: all 0.5s;
    }
    &:hover {
      a {
        color: #ff5700;
      }
      span {
        background: #ff5700;
        opacity: 1;
      }
    }
  }
  &__item {
    position: relative;
    &--active {
      .header-bottom__item-show {
        color: #ff5700;
        &:after {
          transform: rotate(-180deg);
        }
        span {
          background: #ff5700;
          opacity: 1;
        }
      }
      .header-bottom__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      display: flex;
      align-items: center;
      font-size: 15px;
      line-height: 120%;
      font-weight: 600;
      color: #ffffff;
      transition: all 0.5s;
      cursor: pointer;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 6px;
        right: 14px;
        width: 11px;
        height: 7px;
        background-image: url("../img/arrow.webp");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transition: all 0.3s;
      }
      span {
        display: block;
        width: 5px;
        height: 5px;
        background: #ffffff;
        border-radius: 50%;
        opacity: 0.2;
        margin-left: 30px;
        transition: all 0.5s;
      }
    }
    &-hidden {
      position: absolute;
      z-index: 2;
      top: 100%;
      left: 0;
      width: 250px;
      padding-top: 15px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-wrap {
      width: 100%;
      background: #ffffff;
      box-shadow: 4px 4px 42px 1px rgba(34, 60, 80, 0.2);
      padding: 15px 10px;
    }
    &-link {
      display: block;
      font-size: 15px;
      line-height: 120%;
      font-weight: 600;
      color: #000;
      &:not(:first-child) {
        margin-top: 15px;
      }
      &:hover {
        color: #ff5700;
      }
    }
  }
}
.header-mob {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding: 15px 0;
  display: none;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media screen and (max-width: 992px) {
  .header {
    .mail,
    .tel {
      .header-top__item-text {
        font-size: 15px;
      }
    }
  }
  .header-top {
    &__center {
      margin: 0 30px;
    }
    &__logo {
      width: 190px;
    }
    &__item {
      &-text {
        font-size: 12px;
      }
    }
    &__btn {
      width: 165px;
      height: 30px;
      span {
        font-size: 10px;
      }
    }
  }
  .header-bottom {
    &__list {
      li {
        &:not(:first-child) {
          margin-left: 10px;
        }
      }
    }
    &__link {
      font-size: 12.4px;
      a span {
        margin-left: 8px;
      }
    }
    &__item {
      &-show {
        font-size: 12.4px;
        &:after {
          top: 3px;
          right: 8px;
        }
        span {
          margin-left: 18px;
        }
      }
      &-link {
        font-size: 12.5px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header-bottom {
    &__list {
      li {
        &:not(:first-child) {
          margin-left: 7px;
        }
      }
    }
    &__link {
      a span {
        margin-left: 7px;
      }
    }
    &__item {
      &-show {
        &:after {
          right: 6px;
        }
        span {
          margin-left: 17px;
        }
      }
    }
  }
}

@media screen and (max-width: 740px) {
  .header {
    &-burger {
      display: block;
    }
  }
  .header-top {
    &__center {
      display: none;
    }
    &__right {
      display: none;
    }
    &__logo {
      width: 180px;
    }
    &__btn {
      margin-left: 10px;
    }
    &__item {
      &-text {
        font-size: 14px !important;
        margin-left: 5px;
      }
    }
  }
  .header-bottom {
    position: fixed;
    z-index: 10;
    top: 0;
    left: -105%;
    width: 100%;
    height: 100%;
    background-color: #002341;
    padding: 100px 15px 30px;
    transition: all 0.5s;
    &--active {
      left: 0;
    }
    &__list {
      flex-direction: column;
      li {
        span {
          display: none;
        }
        &:not(:first-child) {
          margin: 15px 0 0 0;
        }
      }
    }
    &__item {
      &--active {
        .header-bottom__item-hidden {
          display: block;
        }
      }
      &-show {
        display: inline-flex;
        &:after {
          right: -20px;
        }
      }
      &-hidden {
        position: static;
        opacity: 1;
        width: auto;
        pointer-events: all;
        padding-top: 0;
        display: none;
      }
      &-wrap {
        background: transparent;
        box-shadow: unset;
        padding-top: 10px;
        padding-right: 0;
        padding-bottom: 0;
      }
      &-link {
        color: #ffffff;
      }
    }

  }
  .header-mob {
    display: block;
  }
}

@media screen and (max-width: 400px) {
  .header-top {
    &__btn {
      width: 130px;
      height: 41px;
    }
  }
}