.product-main {
  padding: 40px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    width: 550px;
    margin-right: 50px;
  }
  &__right {
    width: calc(100% - 600px);
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__title {
    font-size: 36px;
    line-height: 120%;
    font-weight: 700;
    color: #222222;
  }
  &__title-mob {
    display: none;
  }
  &__price {
    margin-top: 25px;
    &-title {
      font-size: 16px;
      line-height: 120%;
      color: #222222;
    }
    &-text {
      font-size: 33px;
      line-height: 120%;
      font-weight: 700;
      color: #222222;
      margin-top: 5px;
      span {
        font-size: 55px;
        color: #ff5700;
      }
    }
  }
  &__calc {
    &-wrap {
      margin-top: 25px;
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      color: #222222;
    }
  }
  .cartcalc {
    display: flex;
    align-items: center;
    width: 154px;
    height: 49px;
    background-color: #ffffff;
    border: 1px solid #dde9f8;
    border-radius: 5px;
    margin-top: 10px;
    .ccalc-minus {
      width: 35px;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 30px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.025em;
      color: #68788b;
      cursor: pointer;
      transition: all 0.5s;
      &:hover {
        color: #ff5700;
      }
    }
    .ccalc-plus {
      width: 35px;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 30px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.025em;
      color: #68788b;
      cursor: pointer;
      transition: all 0.5s;
      &:hover {
        color: #ff5700;
      }
    }
    input {
      width: calc(100% - 70px);
      height: 100%;
      margin: 0;
      border: none;
      font-size: 18px;
      line-height: 120%;
      font-weight: 500;
      text-transform: uppercase;
      letter-spacing: 0.025em;
      color: #222222;
      padding: 3px;
      text-align: center;
      border-right: 1px solid #dde9f8;
      border-left: 1px solid #dde9f8;
    }
  }
  &__form {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    &-group {
      margin-bottom: 15px;
      &:not(:last-child) {
        margin-right: 15px;
      }
    }
    input {
      width: 264px;
      height: 49px;
      background-color: #ffffff;
      border: 1px solid #dde9f8;
      color: #aaaaaa;
      &::placeholder {
        color: #000000;
      }
    }
    &-btn {
      width: 237px;
      height: 49px;
      span {
        font-size: 14px;
      }
      i {
        margin-left: 10px;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 40px / 2));
    gap: 20px 40px;
    margin-top: 15px;
  }
  &__items-mob {
    display: none;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-img {
      flex-shrink: 0;
      width: 69px;
      height: 69px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #c9dff2;
      border-radius: 5px;
      img {
        max-width: 46px;
      }
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
      color: #222222;
      margin-left: 15px;
    }
  }
}
.product-info {
  background: #c9e4ff;;
  padding: 40px 0;
  &__title {
    font-size: 22px;
    line-height: 120%;
    font-weight: 600;
    color: #282828;
  }
  &__text {
    margin-top: 15px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: #282828;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__items {
    margin-top: 30px;
  }
  &__item {
    display: flex;
    &:not(:first-child) {
      margin-top: 15px;
    }
    &-icon {
      flex-shrink: 0;
      width: 25px;
      height: 25px;
    }
    &-desc {
      margin-left: 15px;
    }
    &-title {
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      color: #282828;
    }
    &-text {
      font-size: 16px;
      line-height: 140%;
      color: #282828;
      margin-top: 10px;
    }
  }
}
.partners {
  background-color: #00539a;
  padding: 60px 0 100px 0;
  &__title {
    p:nth-child(1) {
      color: #ffffff;
    }
    p:nth-child(2) {
      color: #ffffff;
    }
  }
  &__slider {
    position: relative;
    margin-top: 35px;
  }
  &-slide {
    img {
      width: 100%;
    }
  }
  .swiper-button-prev {
    left: -50px;
    transform: translate(0, -50%) !important;
    margin-top: 0;
  }
  .swiper-button-next {
    right: -50px;
    transform: translate(0, -50%) !important;
    margin-top: 0;
  }
  .swiper-pagination {
    bottom: -50px;
  }
}


@media screen and (max-width: 1300px) {
  .partners {
    &__slider {
      padding: 0 30px;
    }
    .swiper-button-prev {
      left: 0;
    }
    .swiper-button-next {
      right: 0;
    }
  }
}

@media screen and (max-width: 1100px) {
  .product-main {
    &__left {
      width: 420px;
      margin-right: 30px;
    }
    &__right {
      width: calc(100% - 450px);
    }
    &__subtitle {
      font-size: 30px;
    }
    &__price {
      &-title {
        font-size: 14px;
      }
      &-text {
        font-size: 28.4px;
        span {
          font-size: 45px;
        }
      }
    }
    &__form {
      input {
        width: 228px;
        height: 43px;
        font-size: 12px;
      }
      button {
        width: 204px;
        height: 43px;
        span {
          font-size: 12px;
        }
      }
    }
    &__item {
      &-img {
        width: 60px;
        height: 60px;
      }
      &-title {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .product-main {
    &__title {
      font-size: 25px;
    }
    &__price {
      &-title {
        font-size: 12px;
      }
      &-text {
        font-size: 20px;
        span {
          font-size: 35px;
        }
      }
    }
    .cartcalc {
      height: 43px;
    }
  }
  .product-info {
    &__title {
      font-size: 20px;
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
    &__item {
      &-title {
        font-size: 16px;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
  .partners {
    padding: 40px 0 100px;
  }
}

@media screen and (max-width: 940px) {
  .product-main {
    &__left {
      width: 340px;
    }
    &__right {
      width: calc(100% - 370px);
    }
    &__items {
      display: none;
    }
    &__items-mob {
      display: grid;
      margin-top: 25px;
    }
    &__item {
      &-img {
        width: 50px;
        height: 50px;
        img {
          max-width: 30px;
        }
      }
    }
  }
  .partners {
    &__slider {
      padding: 0;
    }
    .swiper-button-prev {
      display: none;
    }
    .swiper-button-next {
      display: none;
    }
  }
}

@media screen and (max-width: 740px) {
  .product-main {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 20px 0;
    }
    &__right {
      width: 100%;
    }
    &__title {
      display: none;
    }
    &__title-mob {
      display: block;
      margin-bottom: 15px;
    }
    &__price {
      margin-top: 0;
    }
    &__items-mob {
      grid-template-columns: repeat(2, calc(100% / 2 - 20px / 2));
      gap: 20px;
    }
  }
}

@media screen and (max-width: 390px) {
  .product-main {
    &__items-mob {
      grid-template-columns: 100%;
    }
  }
}
