.wrap {
  position: relative;
  overflow: hidden;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-orange {
  background-color: #ff5700;
  border: 1px solid #ff5700;
  border-radius: 3px;
  span {
    line-height: 120%;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    color: #ffffff;
  }
  &:hover {
    background-color: #e56b2b;
    border: 1px solid #e56b2b;  }
}
.btn-transparent {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255,87,0,0.0);
  border: 1px solid #ffffff;
  border-radius: 5px;
  position: relative;
  span {
    line-height: 120%;
    font-weight: 500;
    text-transform: uppercase;
    color: #ffffff;
  }
  i {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translate(0, -50%);
    width: 22px;
    img {
      width: 100%;
    }
  }
  &:hover {
    background: #000000;
    border-color: #000000;
  }
}
.swiper-button-prev {
  width: 22px;
  height: auto;
  &:hover {
    svg path {
      fill: #ff5700;
    }
  }
  svg {
    width: 100%;
    height: auto;
    transition: all 0.3s;
  }
  &:after {
    display: none;
  }
}
.swiper-button-next {
  width: 22px;
  height: auto;
  &:hover {
    svg path {
      fill: #ff5700;
    }
  }
  svg {
    width: 100%;
    height: auto;
    transition: all 0.3s;
  }
  &:after {
    display: none;
  }
}
.swiper-button-disabled {
  opacity: 1 !important;
  pointer-events: all !important;
}
.swiper-pagination {
  &-bullet {
    width: 16px;
    height: 16px;
    background-color: rgba(255,96,0,0.0);
    border: 1px solid rgba(255,255,255,0.4);
    border-radius: 3px;
    opacity: 1;
    margin: 0 !important;
    text-align: start;
    &:not(:first-child) {
      margin-left: 18px !important;
    }
  }
  &-bullet-active {
    background-color: #ff6000;
    border: 1px solid #ff6000;
  }
}
.search {
  position: fixed;
  z-index: 10;
  top: -105%;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 35, 65, 0.8);
  padding: 100px 15px 30px;
  transition: all 0.5s;
  &--active {
    top: 0;
  }
  &-close {
    position: absolute;
    top: 25px;
    right: 25px;
    width: 25px;
    height: 25px;
    span {
      cursor: pointer;
    }
    span:nth-child(1) {
      position: absolute;
      top: -2px;
      left: 50%;
      transform: rotate(-45deg) translate(-50%, 0);
      width: 3px;
      height: 100%;
      background: #ffffff;
    }
    span:nth-child(2) {
      position: absolute;
      top: 0;
      left: 50%;
      transform: rotate(45deg) translate(-50%, 0);
      width: 3px;
      height: 100%;
      background: #ffffff;
    }
  }
  &-input {
    width: 600px;
    height: 50px;
    position: relative;
    margin: 0 auto;
    input {
      width: 100%;
      height: 100%;
      background: #ffffff;
      padding: 5px 10px;
      font-size: 16px;
      line-height: 120%;
    }
    button {
      position: absolute;
      top: 0;
      right: 0;
      width: 50px;
      height: 100%;
      background: #002341;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 22px;
        height: auto;
      }
    }
  }
}
.sidebar-menu {
  position: fixed;
  z-index: 10;
  top: 0;
  left: -200%;
  width: 500px;
  height: 100%;
  background-color: #002341;
  padding: 100px 15px 30px;
  transition: all 0.8s;
  &--active {
    left: 0;
  }
  &__list {
    li {
      font-size: 16px;
      line-height: 120%;
      color: #ffffff;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
    li a:hover {
      color: #ff5700;
    }
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 25px;
    height: 25px;
    span {
      cursor: pointer;
    }
    span:nth-child(1) {
      position: absolute;
      top: -2px;
      left: 50%;
      transform: rotate(-45deg) translate(-50%, 0);
      width: 3px;
      height: 100%;
      background: #ffffff;
    }
    span:nth-child(2) {
      position: absolute;
      top: 0;
      left: 50%;
      transform: rotate(45deg) translate(-50%, 0);
      width: 3px;
      height: 100%;
      background: #ffffff;
    }
  }
}
.display2 {
  p:nth-child(1) {
    font-size: 11px;
    line-height: 120%;
    font-weight: 600;
    text-transform: uppercase;
    position: relative;
    padding-left: 35px;
    &:after {
      content: '';
      position: absolute;
      top: 3px;
      left: 0;
      width: 22px;
      height: 3px;
      background-color: #ff5700;
    }
    span {
      color: #ff5700;
    }
  }
  p:nth-child(2) {
    font-size: 35px;
    line-height: 120%;
    font-weight: 800;
    margin-top: 7px;
  }
}
.form {
  margin-top: 25px;
  &-grid {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    gap: 12px 30px;
  }
  &-group {
    padding: 0;
    margin: 0;
    border: none;
    position: relative;
    input {
      background-color: #254764;
      border: 1px solid #517797;
      padding: 5px 15px 5px 40px;
      font-size: 12.2px;
      line-height: 120%;
      color: #ffffff;
      &::placeholder {
        color: #91b3cf;
      }
    }
    textarea {
      background-color: #254764;
      border: 1px solid #517797;
      padding: 5px 15px 5px 40px;
      resize: none;
      font-size: 12.2px;
      line-height: 120%;
      color: #ffffff;
      &::placeholder {
        color: #91b3cf;
      }
    }
    .input-icon {
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 15px;
      transform: translate(0, -50%);
      max-width: 15px;
      img {
        max-width: 100%;
      }
    }
  }
  &-group--33 {
    grid-column: 1/3;
  }
  &-group--100 {
    grid-column: 1/4;
  }
  &-bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 25px;
  }
  &-check {
    display: inline-flex;
    position: relative;
    margin: 0 30px 15px 0;
    cursor: pointer;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
      &:checked ~ label {
        &:before {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    label {
      font-size: 11px;
      line-height: 120%;
      font-weight: 500;
      color: #5483ab;
      opacity: 0.6;
      padding-left: 22px;
      &:after {
        content: '';
        position: absolute;
        top: -2px;
        left: 0;
        width: 15px;
        height: 15px;
        border: 1px solid #517797;
        border-radius: 3px;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 2px;
        transform: translate(0, -50%);
        width: 12px;
        height: 8px;
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s;
      }
      a {
        position: relative;
        z-index: 2;
        color: #ff5700;
      }
    }
  }
  &-btn {
    width: 216px;
    height: 49px;
    margin-bottom: 15px;
  }
  .nice-select {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: #254764 !important;
    border: 1px solid #517797;
    border-radius: 0;
    padding: 5px 15px 5px 40px;
    .current {
      font-size: 12.2px;
      line-height: 120%;
      color: #91b3cf;
    }
    .list {
      li {
        background-color: #254764;
        border-top: 1px solid #000000;
        padding: 12px;
        color: #ffffff;
      }
    }
    .option.selected.focus {
      background-color: #002341;
    }
  }
  .cartcalc {
    display: flex;
    align-items: center;
    background-color: #254764;
    border: 1px solid #517797;
    position: relative;
    .ccalc-minus {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 45px;
      height: 100%;
      font-size: 30px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.025em;
      color: #517797;
      cursor: pointer;
      transition: all 0.5s;
      &:hover {
        color: #ff5700;
      }
    }
    .ccalc-plus {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 45px;
      height: 100%;
      font-size: 30px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.025em;
      color: #517797;
      cursor: pointer;
      transition: all 0.5s;
      &:hover {
        color: #ff5700;
      }
    }
    input {
      width: calc(100% - 90px);
      height: 100% !important;
      padding: 5px;
      font-size: 12.2px;
      line-height: 120%;
      color: #254764;
      border: none;
      border-right: 1px solid #517797;
      border-left: 1px solid #517797;
      text-align: center;
      &::placeholder {
        color: #91b3cf;
      }
      &:focus {
        color: #ffffff;
        & ~ .input-placeholder {
          display: none;
        }
      }
    }
    .input-placeholder {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 12.2px;
      line-height: 120%;
      color: #ffffff;
      background: #254764;
      text-align: center;
      pointer-events: none;
      &::placeholder {
        color: #91b3cf;
      }
      &--active {
        display: none;
        & ~ input {
          color: #ffffff;
        }
      }
    }
  }
}
.breadcrumbs {
  border-bottom: 1px solid rgba(248, 237, 230, 0.4);;
  padding-top: 15px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    font-size: 15px;
    line-height: 140%;
    font-weight: 500;
    color: #ff5700;
    margin-bottom: 15px;
    position: relative;
    &--active {
      color: #ffffff;
    }
    &:not(:last-child) {
      margin-right: 30px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -22px;
        transform: translate(0, -50%);
        width: 10px;
        height: 6px;
        background-image: url("../img/breadcrumbs-icon.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
    }
  }
}
.navigation {
  padding: 50px 15px;
  &__list {
    li {
      font-size: 20px;
      line-height: 140%;
      &:not(:first-child) {
        margin-top: 15px;
      }
      &:hover {
        color: #ff5700;
      }
    }
  }
}
.page-top {
  background-image: url("../img/page-top__bg.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__inner {
    padding: 70px 0;
  }
  &__title {
    font-size: 51px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 29px;
      left: -30px;
      width: 22px;
      height: 3px;
      background-color: #ff5700;
    }
  }
}


@media screen and (max-width: 992px) {
  .swiper-pagination {
    &-bullet {
      width: 13px;
      height: 13px;
    }
  }
  .display2 {
    p:nth-child(1) {
      font-size: 9px;
      padding-left: 22px;
      &:after {
        top: 3px;
        width: 18px;
        height: 2px;
      }
    }
    p:nth-child(2) {
      font-size: 29px;
    }
  }
  .btn-transparent {
    i {
      width: 18px;
    }
  }
  .form {
    &-grid {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
      gap: 12px 20px;
    }
    &-group {
      input {
        font-size: 10px;
      }
      textarea {
        font-size: 10px;
      }
    }
    &-btn {
      width: 178px;
      height: 40px;
      span {
        font-size: 12px;
      }
    }
    .cartcalc {
      .ccalc-plus {
        font-size: 24px;
      }
      .ccalc-minus {
        font-size: 24px;
      }
    }
  }
  .breadcrumbs {
    &__item {
      font-size: 13px;
    }
  }
  .page-top {
    &__inner {
      padding: 50px 0;
    }
    &__title {
      font-size: 42px;
      &:after {
        top: 26px;
        left: -22px;
        width: 18px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .page-top {
    &__title {
      font-size: 28px;
      &:after {
        top: 15px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .display2 {
    p:nth-child(2) {
      font-size: 24px;
    }
  }
  .form {
    &-grid {
      grid-template-columns: 100%;
    }
    &-group--100 {
      grid-column: 1/1;
    }
    &-group--33 {
      grid-column: 1/1;
    }
    &-bottom {
      display: block;
    }
    &-check {
      margin: 0;
    }
    &-btn {
      margin: 15px auto 0;
    }

  }
}